	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"\"The most dangerous thought you can have as a creative person is to think you know what you're doing.\"\nPresented at Dropbox's DBX conference on July 9, 2013.\nAll of the slides are available at: http://worrydream.com/dbx/\n\nFor his recent DBX Conference talk, Victor took attendees back to the year 1973, donning the uniform of an IBM systems engineer of the times, delivering his presentation on an overhead projector. The '60s and early '70s were a fertile time for CS ideas, reminds Victor, but even more importantly, it was a time of unfettered thinking, unconstrained by programming dogma, authority, and tradition. 'The most dangerous thought that you can have as a creative person is to think that you know what you're doing,' explains Victor. 'Because once you think you know what you're doing you stop looking around for other ways of doing things and you stop being able to see other ways of doing things. You become blind.' He concludes, 'I think you have to say: \"We don't know what programming is. We don't know what computing is. We don't even know what a computer is.\" And once you truly understand that, and once you truly believe that, then you're free, and you can think anything.'\"","meta":{"duration":"PT32M56S"},"presenter":{"name":"Bret Victor"},"source":{"id":"8pTEmbeENF4","provider":"youtube"},"slug":"the-future-of-programming","tag":["Inspiring","Funny","DBX conference 2013"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/8pTEmbeENF4/default.jpg","height":90,"width":120}},"title":"The Future of Programming"}},{"node":{"description":"Bret Victor invents tools that enable people to understand and create. He has designed experimental UI concepts at Apple, interactive data graphics for Al Gore, and musical instruments at Alesis.","meta":{"duration":"PT54M20S"},"presenter":{"name":"Bret Victor"},"source":{"id":"PUv66718DII","provider":"youtube"},"slug":"inventing-on-principle","tag":["Inspiring"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/PUv66718DII/default.jpg","height":90,"width":120}},"title":"Inventing on Principle"}},{"node":{"description":"An incredible talk by Bret Victor about the essence of digital art.","meta":{"duration":"PT53M33S"},"presenter":{"name":"Bret Victor"},"source":{"id":"ZfytHvgHybA","provider":"youtube"},"slug":"stop-drawing-dead-fish","tag":["Inspiring","Digital Art"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/ZfytHvgHybA/default.jpg","height":90,"width":120}},"title":"Stop Drawing Dead Fish"}}]}},"pathContext":{"speaker":"Bret Victor"}}

